#cartContainer{
    flex: 7.5;
    display: flex;
    flex-wrap: wrap;
    overflow: scroll;
    padding: 5vmin;
    gap: 5vmin;
}

    #cartChild0{
        width: 100%;
        height: 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }

        #cartChild0Child0{
            z-index: 1;
            font-size: 6vmin;
        }

    #cartChild1{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 5vmin;
    }

        #cartChild1Child0{
            width: 100%;
            height: 50%; 
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 5%;
        }

            #cartChild1Child0ImgCon{
                width: 25%;
                height: 100%;
                position: relative;
            }

            #cartChild1Child0Name{
                width: 15%;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            #cartChild1Child0Price{
                width: 15%;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            #cartChild1Child0Cart{
                width: 15%;
                display: flex;
                justify-content: center;
                align-items: center;
            }

    #cartChild3{
        width: 100%;
        height: 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 1;
    }

        #cartChild3Child0{
            z-index: 1;
            font-size: 5vmin;
        }
