#menuContainerHidden{
    display: none;
}

#menuContainerShow{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: row-reverse;
    z-index: 2;
}

    #menuChild0{
        flex: 1;
        display: flex;
        padding: 5vmin;
        gap: 5vmin;
        background-color: black;
        opacity: 65%;
    }

        #menuChild0Child0{
            height: 10%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        #menuChild0Child1{
            flex: 5;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        #menuChild0Child2{
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: black;
        }

    #menuChild1{
        flex: 2;
        display: flex;
        flex-direction: column;
        padding: 5vmin;
        gap: 5vmin;       
        background-color: rgb(35, 35, 35);
    }

        #menuChild1Child0{
            width: 100%;
            height: 10%;
            display: flex;
            align-items: center;
        }

        #menuChild1Child2{
            width: 100%;
            height: 10%;
            display: flex;
            align-items: center;
        }

    #menuChild2{
        flex: 1;
        display: flex;
        padding: 5vmin;
        gap: 5vmin;      
    }

        #menuChild2Child0{
            flex: 1;
            background-color: darkslateblue;
        }
