#shopContainer{
    flex: 7.5;
    display: flex;
    flex-direction: column;
    padding: 5vmin;
    gap: 5vmin;
}

    #shopChild0{
        width: 100%;
        height: 15%;
        display: flex;
        align-items: center;
    }

    #shopChild1{
        flex: 1;
        display: flex;
        position: relative;
        flex-wrap: wrap;
        overflow: scroll;
        flex-direction: row;
        justify-content: center;
        gap: 5vmin;
    }

        .shopChild1Child0{
            width: 42.5%;
            height: 55%;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

            .shopChild1Child0ImgCon{
                flex: 10;
                display: flex;
                position: relative;
            }

            .shopChild1Child0Name{
                flex: 2;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                font-size: 5vmin;
            }

            .shopChild1Child0Price{
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .shopChild1Child0View{
                width: 100%;
                height: 100%;
                z-index: 1;
                opacity: 0%;
            }

        #shopChild1Child1{
            width: 100%;
            height: 25%;
            margin-bottom: 5vmin;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
        }

        #shopChild1Child2{
            width: 100%;
            height: 25%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-size: 5vmin;
            z-index: 1;
        }

        #shopChild1Child4{
            width: 100%;
            height: 25%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            position: relative;
            font-size: 4vmin;
        }