html, body{
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
  display: flex;
  padding: 0;
  margin: 0;
  background-color: rgb(35, 35, 35);
  color: white;
  font-family: 'Righteous', cursive;
  font-size: 4vmin;
}

#root{
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

a{
  text-decoration: none;
  color: white;
}

button{
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  border-style: none;
  color: white;
  font-family: 'Righteous', cursive;
  font-size: 4vmin;
  background-color: rgb(35, 35, 35);
}

img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}